import { assert } from 'ember-debug';
import { Object as EmberObject } from 'ember-runtime';
import { FACTORY_FOR } from 'container';

export default EmberObject.extend({
  componentFor: function (name, owner, options) {
    (true && assert('You cannot use \'' + name + '\' as a component name. Component names must contain a hyphen.', ~name.indexOf('-')));

    var fullName = 'component:' + name;
    return owner[FACTORY_FOR](fullName, options);
  },
  layoutFor: function (name, owner, options) {
    (true && assert('You cannot use \'' + name + '\' as a component name. Component names must contain a hyphen.', ~name.indexOf('-')));


    var templateFullName = 'template:components/' + name;
    return owner.lookup(templateFullName, options);
  }
});