import { assert, Error as EmberError } from 'ember-debug';

function parseUnderscoredName(templateName) {
  var nameParts = templateName.split('/');
  var lastPart = nameParts[nameParts.length - 1];

  nameParts[nameParts.length - 1] = '_' + lastPart;

  return nameParts.join('/');
}

export default function lookupPartial(templateName, owner) {
  if (templateName == null) {
    return;
  }

  var template = templateFor(owner, parseUnderscoredName(templateName), templateName);

  (true && assert('Unable to find partial with name "' + templateName + '"', !!template));


  return template;
}

export function hasPartial(name, owner) {
  if (!owner) {
    throw new EmberError('Container was not found when looking up a views template. ' + 'This is most likely due to manually instantiating an Ember.View. ' + 'See: http://git.io/EKPpnA');
  }

  return owner.hasRegistration('template:' + parseUnderscoredName(name)) || owner.hasRegistration('template:' + name);
}

function templateFor(owner, underscored, name) {
  if (!name) {
    return;
  }
  (true && assert('templateNames are not allowed to contain periods: ' + name, name.indexOf('.') === -1));


  if (!owner) {
    throw new EmberError('Container was not found when looking up a views template. ' + 'This is most likely due to manually instantiating an Ember.View. ' + 'See: http://git.io/EKPpnA');
  }

  return owner.lookup('template:' + underscored) || owner.lookup('template:' + name);
}